.page {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 90vh;
}

.title {
    font-weight: 500;
    text-align: center;
    color: #2D7C73;
}

.heading {
    font-family: 'Rubik', sans-serif;
    font-weight: 600;
    text-align: center;
    color: #2D7C73;
}

.subheading {
    font-family: 'Quicksand', sans-serif;
    font-weight: 550;
    text-align: center;
    color: #ADADAD;
}

.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position:fixed;
    top:0;
    left:0;
    width: 100%;
}

.name {
    font-family: 'Rubik-Regular', sans-serif;
    font-weight: 300;
    color: #2D7C73;
    margin: 0;
}

.round {
    font-family: 'quicksand', sans-serif;
    font-weight: 300;
    color: #ADADAD;
    margin: 0;
}

.button {
    margin-left: auto;
    margin-right: auto;
}

.container {
    background-color: #DCEBE1;
    filter: drop-shadow(0 0.4rem 0.2rem rgba(0, 0, 0, 0.25));
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

@media (orientation: portrait) {
    .title {
        font-size: 20vw;
        margin-top: -10vw;
        margin-bottom: 1vw;
    }

    .logo {
        height: min(10vw, 20vh);
        margin-bottom: min(8vw, 8vh);
    }

    .heading {
        font-size: 10vw;
        letter-spacing: 1.5vw;
        margin-bottom: min(5vw, 5vh);
    }

    .faux_header {
        height: 8.68vh;
        width: 99vw;
    }

    .subheading {
        font-size: 9vw;
        letter-spacing: 0.7vw;
    }

    .header{
        margin-top: 3vw;
    }

    .name {
        margin-left: 3vw;
        font-size: 10vw;
    }

    .round {
        font-size: 6vw;
        margin-right: 3vw;
    }

    .container {
        width: fit-content;
        height: fit-content;
        border-radius: min(7.2vw);
        padding: min(5vw, 6vh);
        margin: 4.5vw;
    }
    .spacer{
        height: 4vw;
    }
}

@media (orientation: landscape) {
    .title{
        font-size: 6vw;
    }

    .logo {
        height: min(5vw, 15vh);
    }

    .heading {
        font-size: 3vw;
        letter-spacing: 0.4vw;
        padding-top: 3vw;
        padding-bottom: 1.5vw;
    }

    .faux_header {
        height: 9.25vh;
        width: 99vw;
    }

    .subheading {
        font-size: 4.3vw;
        letter-spacing: 0.4vw;
    }

    .header{
        margin-top: 2vh;
        margin-right: 1vh;
    }

    .name {
        margin-left: 4vh;
        margin-top: 0.5vh;
        margin-bottom: 0.5vh;
        font-size: 5.5vh;
    }

    .round {
        margin-right: 4vh;
        font-size: 4.5vh;
    }

    .container{
        width: fit-content;
        min-width: 18vw;
        min-height: calc(0.3 * 35vw);
        padding: max(1.3vw, 1.7vh);
        height: max(5vw, 5vh);
        border-radius: 10% / 20%;
        margin: min(1vw) min(2vw) min(2vw) min(2vw);
    }
    .spacer{
        height: 0;
    }
}
